<template>
  <div class="secure">
    <van-nav-bar title="安全中心" left-text="返回" left-arrow :fixed="true" @click-left="returnLast" />
    <div class="secure-bg"></div>
    <div class="secure-box">
      <div>
        <van-cell title="修改密码" isLink @click="openPasswordPopup" />
        <van-cell title="注销账号" isLink @click="destroyAccount" />
      </div>
    </div>
    <div class="btn-box">
      <van-button round block color="#0C34BA" @click="loginOut">退出登录</van-button>
    </div>
    <van-popup class="popup-box" closeable v-model="isOpen" position="bottom">
      <div class="popup-item">
        <div class="password-text">修改密码</div>
        <van-form>
          <van-field
            v-for="(item, index) in passwords"
            :key="index"
            :type="item.isPassword ? 'password' : 'text'"
            :label="item.title"
            placeholder="6-16位密码且以字母开头"
            v-model="item.password"
            :right-icon="item.isPassword ? 'closed-eye' : 'eye-o'"
            @click-right-icon="viewPassword(item)"
          />
          <div class="login-btn">
            <van-button
              round
              block
              color="#0C34BA"
              native-type="submit"
              @click="confirmPassword"
            >确认修改</van-button>
          </div>
        </van-form>
      </div>
    </van-popup>
  </div>
</template>

<script>
import { utils } from "../assets/js/utils";
import { validForm } from "../assets/js/validForm";
export default {
    data() {
        return {
            isOpen: false,
            passwords: {
                old:{
                    password: '',
                    isPassword: true,
                    title: '旧密码',
                },
                new: {
                    password: '',
                    isPassword: true,
                    title:'新密码'
                }
            }
        }
    },

    created(){
       //是否登录
        this.isLogin()
    },

    methods:{
        //返回上一级
        returnLast(){
            this.$router.go(-1)
        },
        //是否登录
        isLogin(){
          let tokenString = localStorage.getItem('CSTK');
          if (!tokenString) {
            return this.$router.push({name:'Login'})
          }
        },
        //退出登录
        loginOut(){
            this.$dialog.confirm({
                title: '退出登录',
                message: '是否确认退出登录！',
                confirmButtonColor: '#0C34BA',
            }).then(()=>{
                this.$toast.loading({
                    message: "加载中...",
                    forbidClick: true,
                    duration: 0,
                    loadingType: "spinner",        
                });
            localStorage.removeItem('CSTK');

            setTimeout(() => {
                this.$toast.clear();
                this.$router.push({name:'Login'});
            }, 1000);
            })
        },

        //修改密码
        openPasswordPopup(){
            this.isOpen = true;
        },

        //确认修改密码
        confirmPassword(){
            //获取token字符串
            let tokenString = localStorage.getItem('CSTK');
            if (!tokenString) {
                return this.$router.push({name:'Login'});
            }

            let o = {
                oldPassword:{
                    value:this.passwords.old.password,
                    reg:/^[A-Za-z]\w{5,15}$/,
                    errorMsg:'旧密码支持字幕数字下划线组合',
                },
                password:{
                    value:this.passwords.new.password,
                    reg:/^[A-Za-z]\w{5,15}$/,
                    errorMsg:'旧密码支持字幕数字下划线组合',
                }
            };

            if (!validForm.valid(o)) {
                return;
            }

            this.$toast.loading({
                message: "加载中...",
                forbidClick: true,
                duration: 0,
                loadingType: "spinner",
            });

            let data = utils.queryString({
                appkey: this.appkey,
                tokenString,
                oldPassword: this.passwords.old.password,
                password: this.passwords.new.password,
            });

            this.axios({
                method: 'POST',
                url: 'updatePassword',
                data,
            }).then(result =>{
                this.$toast.clear();
                if (result.data.code == 'E001') {
                    localStorage.removeItem('CSTK');
                    this.$router.push({name:'Login'});
                }else{
                    this.$notify({
                    type: "warning",
                    message: result.data.msg,
                  });      
                }
            }).catch(err =>{
                this.$toast.clear();
                
            })
        },

        //注销账号
        destroyAccount(){
            this.$dialog.confirm({
                title: "注销账号",
                message: "是否确认注销号，一旦确认无法恢复！",
                confirmButtonColor: "#0C34BA",
            }).then(()=>{
                

                let tokenString = localStorage.getItem('CSTK');
                if (!tokenString) {
                    return this.$router.push({name:'Login'});
                }

                 this.$toast.loading({
                 message: "加载中...",
                 forbidClick: true,
                 duration: 0,
                 loadingType: "spinner",
          });       

                let data = utils.queryString({
                    appkey:this.appkey,
                    tokenString
                });

                this.axios({
                    method:'POST',
                    url:'destoryAccount',
                    data
                }).then(result =>{
                    this.$toast.clear();
                    
                    this.$toast(result.data.msg);
                    if (result.data.code == 'G001') {
                        localStorage.removeItem('CSTK');
                        setTimeout(() => {
                            this.$router.push({name:'Login'})
                        }, 600);
                    }
                }).catch(err =>{
                    this.$toast.clear();
                    
                })
            }).catch(()=>{
                
            })
        }
    }
};
</script>

<style lang="less" scoped>
.secure{
  padding-top: 46px;
  /deep/ .van-nav-bar__text{
    color: #0C34BA;
  }

  /deep/ .van-nav-bar .van-icon{
    color: #0C34BA;
  }

  .secure-bg{
    height: 100px;
    background-color: #0C34BA;
  }

  .secure-box{
    margin: -30px 10px 0;
    border-radius: 10px;
    overflow: hidden;
    background-color: #f5f5f5;
    padding: 10px;
  }

  .btn-box{
    margin-top: 100px;
    padding: 10px;
  }

  .password-text{
    font-size: 18px;
    margin-bottom: 30px;
  }
  .popup-item{
    padding: 20px;
  }

  .login-btn{
    margin-top: 40px;
  }
  .popup-box{
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
  }
}
</style>